"use client"

import type React from "react"
import { useState, useRef, useEffect } from "react"
import { useChat } from "ai/react"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Send, Paperclip, X, FileText, Copy, Check, Loader2, User, Sparkles } from "lucide-react"
import { cn } from "@/lib/utils"
import { toast } from "sonner"

// ----------------------------------------------------------------------
// 1. 内置的高端渲染器 (纯手写，不依赖任何外部插件，绝对稳)
// ----------------------------------------------------------------------
function SafeHighEndRenderer({ content }: { content: string }) {
  if (!content) return <span className="animate-pulse">▌</span>; // 光标闪烁效果

  // 表格渲染逻辑
  if (content.includes("|") && content.includes("---") && content.split("\n").length > 2) {
    try {
      const lines = content.trim().split("\n").filter(l => l.trim() !== "");
      const headerLineIndex = lines.findIndex(l => l.includes("|") && !l.includes("---"));
      
      if (headerLineIndex !== -1) {
        const headers = lines[headerLineIndex].split("|").filter(c => c.trim()).map(c => c.trim());
        const bodyLines = lines.slice(headerLineIndex + 1).filter(l => l.includes("|") && !l.includes("---"));

        return (
          <div className="space-y-4">
             {/* 渲染表格前的文字 */}
            {headerLineIndex > 0 && (
               <div className="whitespace-pre-wrap text-sm leading-7 text-gray-700">
                 {lines.slice(0, headerLineIndex).join("\n")}
               </div>
            )}
            
            <div className="my-4 overflow-hidden rounded-xl border border-gray-200 shadow-sm">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-[#1a2e35] text-white">
                  <tr>
                    {headers.map((h, i) => (
                      <th key={i} className="px-5 py-3 text-left text-sm font-semibold tracking-wider">{h}</th>
                    ))}
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-100">
                  {bodyLines.map((line, i) => {
                    const cells = line.split("|").filter(c => c.trim()).map(c => c.trim());
                    return (
                      <tr key={i} className="hover:bg-gray-50 transition-colors">
                        {cells.map((cell, j) => (
                          <td key={j} className="px-5 py-3 text-sm text-gray-700 leading-relaxed">
                            {cell.split("**").map((part, k) => k % 2 === 1 ? <span key={k} className="font-bold text-[#0F766E]">{part}</span> : part)}
                          </td>
                        ))}
                      </tr>
                    )
                  })}
                </tbody>
              </table>
            </div>
            
            {/* 渲染表格后的文字 (如果有) */}
             <div className="whitespace-pre-wrap text-sm leading-7 text-gray-700">
                 {/* 简单起见，这里只渲染非表格部分，或者你可以在这里继续处理后续文本 */}
             </div>
          </div>
        )
      }
    } catch (e) {
      // 失败回退
    }
  }

  // 普通文本渲染
  return (
    <div className="space-y-3">
      {content.split("\n").map((line, index) => {
        if (line.trim().startsWith("## ")) return <h3 key={index} className="mt-6 mb-3 text-xl font-bold text-slate-900">{line.replace(/#+\s/, "")}</h3>
        if (line.trim().startsWith("### ")) return <h4 key={index} className="mt-4 mb-2 text-lg font-bold text-slate-800">{line.replace(/#+\s/, "")}</h4>
        if (line.trim() === "---") return <hr key={index} className="my-6 border-gray-200" />
        if (line.trim() === "") return <div key={index} className="h-2"></div>
        
        return (
          <p key={index} className="text-[15px] leading-7 text-gray-700">
            {line.split("**").map((part, i) => 
              i % 2 === 1 ? <span key={i} className="font-bold text-[#0F766E] bg-teal-50 px-1 rounded">{part}</span> : part
            )}
          </p>
        )
      })}
    </div>
  )
}

// ----------------------------------------------------------------------
// 2. 内置的 Loading 组件 (防止因缺少文件导致白屏)
// ----------------------------------------------------------------------
function SimpleLoading() {
  return (
    <div className="flex items-center gap-3 p-4 text-sm text-gray-500 bg-gray-50 rounded-xl border border-dashed border-gray-200 animate-pulse">
      <Loader2 className="h-4 w-4 animate-spin text-primary" />
      <span>AI 老师正在阅读您的作文并思考批改建议...</span>
    </div>
  )
}

// ----------------------------------------------------------------------
// 3. 主界面组件
// ----------------------------------------------------------------------
type UploadedFile = {
  name: string
  type: string
  size: number
  data: string
  preview?: string
}

export function EnhancedChatInterface() {
  const [userId, setUserId] = useState<string>("")
  
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const userStr = localStorage.getItem('currentUser')
      if (userStr) {
        try {
          const user = JSON.parse(userStr)
          setUserId(user.id || user.sub || "")
        } catch (e) {}
      }
    }
  }, [])

  const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([])
  const [copiedMessageId, setCopiedMessageId] = useState<string | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const textareaRef = useRef<HTMLTextAreaElement>(null)

  const { messages, input, handleInputChange, handleSubmit, isLoading, append } = useChat({
    api: '/api/chat',
    body: { userId, files: uploadedFiles },
    onError: (error) => {
      toast.error("出现错误，请检查网络或积余额")
    }
  })

  // 始终自动滚动到底部
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages, isLoading])

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files
    if (!files || files.length === 0) return
    const newFiles: UploadedFile[] = []
    for (let i = 0; i < files.length; i++) {
      const file = files[i]
      const reader = new FileReader()
      const fileData = await new Promise<string>((resolve) => {
        reader.onload = (e) => resolve(e.target?.result as string)
        reader.readAsDataURL(file)
      })
      newFiles.push({
        name: file.name, type: file.type, size: file.size, data: fileData,
        preview: file.type.startsWith("image/") ? fileData : undefined
      })
    }
    setUploadedFiles((prev) => [...prev, ...newFiles])
    if (fileInputRef.current) fileInputRef.current.value = ""
  }

  const removeFile = (index: number) => setUploadedFiles((prev) => prev.filter((_, i) => i !== index))

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!userId) { toast.error("未登录"); return }
    if (!input.trim() && uploadedFiles.length === 0) return

    // 如果只有图片，手动发送一条消息
    if (!input.trim() && uploadedFiles.length > 0) {
       await append({
         role: 'user',
         content: "请帮我批改这份作业", 
         // @ts-ignore
         experimental_attachments: uploadedFiles.map(f => ({ name: f.name, contentType: f.type, url: f.data }))
       })
       setUploadedFiles([])
       return
    }

    handleSubmit(e)
    setUploadedFiles([])
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault()
      onSubmit(e as unknown as React.FormEvent)
    }
  }

  const copyToClipboard = async (text: string, messageId: string) => {
    await navigator.clipboard.writeText(text)
    setCopiedMessageId(messageId)
    setTimeout(() => setCopiedMessageId(null), 2000)
  }

  return (
    <div className="flex h-screen flex-col bg-[#F9FAFB]">
      <ScrollArea className="flex-1 p-4">
        <div className="mx-auto max-w-4xl space-y-8 pb-10">
          {messages.length === 0 ? (
            <div className="flex flex-col items-center justify-center py-20 text-center">
              <div className="mb-6 flex h-20 w-20 items-center justify-center rounded-3xl bg-white shadow-lg shadow-gray-200/50">
                <FileText className="h-10 w-10 text-primary" />
              </div>
              <h1 className="mb-4 text-3xl font-bold tracking-tight text-gray-900">你好！欢迎使用沈翔智学！</h1>
              <p className="max-w-lg text-gray-500">上传作文图片，AI 老师为您深度批改。</p>
            </div>
          ) : (
            <div className="space-y-6">
              {messages.map((message) => (
                <div key={message.id} className={cn("flex gap-4", message.role === "user" ? "justify-end" : "justify-start")}>
                  {message.role === "assistant" && (
                    <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-primary text-white shadow-md">
                      <Sparkles className="h-5 w-5" />
                    </div>
                  )}
                  
                  <div className={cn(
                      "relative overflow-hidden rounded-2xl px-5 py-4 shadow-sm",
                      message.role === "user" ? "bg-primary text-primary-foreground max-w-[80%]" : "bg-white text-gray-800 border border-gray-100 max-w-full w-full"
                    )}>
                    {message.role === "user" ? (
                      <div className="whitespace-pre-wrap text-sm leading-7">{message.content}</div>
                    ) : (
                      // 使用内置渲染器
                      <SafeHighEndRenderer content={message.content} />
                    )}

                    {message.role === "assistant" && message.content && (
                      <div className="mt-4 flex items-center justify-end gap-2 border-t border-gray-100 pt-3">
                        <Button variant="ghost" size="sm" className="h-8 gap-1.5 text-xs text-gray-400 hover:text-primary" onClick={() => copyToClipboard(message.content, message.id)}>
                          {copiedMessageId === message.id ? <Check className="h-3.5 w-3.5" /> : <Copy className="h-3.5 w-3.5" />} 复制
                        </Button>
                      </div>
                    )}
                  </div>
                  {message.role === "user" && <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-gray-200 shadow-sm"><User className="h-5 w-5 text-gray-600" /></div>}
                </div>
              ))}
              
              {/* ✨ 关键修复：如果正在加载，且最后一条消息是用户发的，显示 Loading 组件 */}
              {isLoading && messages[messages.length - 1]?.role === 'user' && (
                <div className="flex gap-4 justify-start">
                   <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-primary text-white shadow-md">
                      <Sparkles className="h-5 w-5" />
                   </div>
                   <div className="max-w-full w-full">
                     <SimpleLoading />
                   </div>
                </div>
              )}
              
              <div ref={messagesEndRef} />
            </div>
          )}
        </div>
      </ScrollArea>

      <div className="border-t border-gray-100 bg-white p-4 pb-6 shadow">
        <div className="mx-auto max-w-4xl">
          {uploadedFiles.length > 0 && (
            <div className="mb-3 flex flex-wrap gap-2">
              {uploadedFiles.map((file, index) => (
                <div key={index} className="flex items-center gap-2 rounded-lg border border-gray-200 bg-gray-50 px-3 py-2 text-sm">
                  {file.preview ? <img src={file.preview} alt="" className="h-8 w-8 rounded object-cover" /> : <FileText className="h-4 w-4" />}
                  <span className="max-w-[100px] truncate">{file.name}</span>
                  <button onClick={() => removeFile(index)}><X className="h-3 w-3" /></button>
                </div>
              ))}
            </div>
          )}

          <form onSubmit={onSubmit} className="relative flex gap-3">
             <Button type="button" variant="outline" size="icon" className="h-12 w-12 rounded-xl" onClick={() => fileInputRef.current?.click()}>
                <Paperclip className="h-5 w-5" />
              </Button>
              <input ref={fileInputRef} type="file" className="hidden" accept="image/*" multiple onChange={handleFileUpload} />
              
              <div className="relative flex-1">
                <Textarea ref={textareaRef} value={input} onChange={handleInputChange} onKeyDown={handleKeyDown} placeholder={userId ? "在此输入..." : "请登录..."} className="min-h-[48px] w-full rounded-xl" rows={1} />
                <Button type="submit" size="icon" className="absolute right-1.5 top-1.5 h-9 w-9 rounded-lg" disabled={isLoading}>
                  {isLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : <Send className="h-4 w-4" />}
                </Button>
              </div>
          </form>
        </div>
      </div>
    </div>
  )
}